import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import "@/assets/css/common/index.scss" // 公共css
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css' //element组件库
import '@/assets/icon' // svg组件
import Vant from 'vant';
import 'vant/lib/index.css'; // vant组件库

// 滚动动画
import AOS from "aos";
import "../node_modules/aos/dist/aos.css";
// AOS.init({
//   offset: 0,
//   duration: 600, //持续时间
//   easing: 'ease-in-sine',
//   delay: 100
// })
AOS.init({
  // 全局设置
  disable: false, // 禁用条件 'phone', 'tablet', 'mobile', boolean, 表达式 或 函数
  startEvent: 'DOMContentLoaded', // 初始化的事件名称
  initClassName: 'aos-init',
  animatedClassName: 'aos-animate',
  useClassNames: false,
  disableMutationObserver: false,
  debounceDelay: 50, // 调整窗口大小时为防止抖动延迟时间
  throttleDelay: 99, // 滚动页面时 延迟时间

  offset: 0, // 是立刻触发动画还是在指定时间之后触发动画
  delay: 0, // 动画的延迟时间
  duration: 2000, // 动画持续时间
  easing: 'ease', // 动画的easing动画效果
  once: false, // 动画是否只会触发一次,或者每次上下滚动都会触发
  mirror: false, // 锚元素。使用它的偏移来取代实际元素的偏移来触发动画
  anchorPlacement: 'top-bottom', // 锚位置,触发动画时元素位于屏幕的位置
});



Vue.use(AOS)
Vue.use(Vant)
Vue.use(ElementUI)
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')