<template>
  <div class=" web-mw">
    <div class="web-header-wrap">
      <div class="header-out-wrap">
        <div class="header-wrap web-w flex flex-x-between flex-item-center">
          <a @click="jumpHome" href="javascript:;" class="logo">
            <img src="@/assets/img/logo.png" alt="">
          </a>
          <div class="nav flex1">
            <ul class="nav-ul">
              <li v-for="(v, k) in nav" @click="navSwitch(v)" :class="currentNav.id == v.id ? 'active' : ''">
                {{ v.name }}
              </li>
            </ul>
          </div>
          <div class="button">
            <el-button size="mini" @click="$router.push('/web/contact')"
              style="background: 0;color: #fff;border-color:#fff;" type="primary">
              立即咨询
            </el-button>
            <el-button size="mini" style="width:80px;">登录</el-button>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    return {
      currentNav: { id: 1 },
      nav: [
        { id: 1, name: '首页', path: '/web/home' },
        { id: 2, name: '客户服务', path: '/web/customer' },
        { id: 20, name: '行业案例', path: '/web/success' },
        { id: 3, name: '定价', path: '/web/price' },
        { id: 4, name: '联系我们', path: '/web/me' },
        // { id: 5, name: 'Test', path: '/web/test' },
      ]
    }
  },
  methods: {
    changeNav2(pre, next) {
      this.banner2CurrentIndex = pre;
    },
    // 切换第二个轮播
    checkBanner2(ind) {
      this.$refs.carousel.setActiveItem(ind)
    },
    navSwitch(v) {
      if (this.currentNav && this.currentNav.id == v.id) return;
      this.$router.push(v.path)
      this.currentNav = v;
      sessionStorage.setItem('currentWebNavId', v.id)
    },
    // 跳转首页
    jumpHome() {
      sessionStorage.setItem('currentWebNavId', 1)
      window.location.href = "/"
    }
  },
  mounted() {
    let id = sessionStorage.getItem("currentWebNavId")
    if (id) {
      this.currentNav.id = id;
    }
  }
}
</script>
